import { ClientTestMaker } from "../../../types/client-test-maker";

const globalTestMakerName =`__TEST__MAKER__`
const tm: ClientTestMaker = window[`${globalTestMakerName}`] as ClientTestMaker;

let clipboardToIE11Formatting: { default: string; "text/html": string; "text/plain": string } = {
    "text/plain": `Text`,
    "text/html": `Url`,
    "default": `Text`
};

function deselectCurrent() {
    let selection: Selection | null = document.getSelection();

    if (!selection?.rangeCount) {
        return function () {
        };
    }
    let active: Element | null = document.activeElement;

    let ranges: any[] = [];

    for (let i = 0; i < selection.rangeCount; i++) ranges.push(selection.getRangeAt(i));

    switch (active?.tagName.toUpperCase()) { // .toUpperCase handles XHTML
        case 'INPUT':
        case 'TEXTAREA':
            active.blur();
            break;

        default:
            active = null;
            break;
    }

    selection.removeAllRanges();

    return function ():void {
        selection?.type === `Caret` && selection.removeAllRanges();

        if (!selection?.rangeCount) {
            ranges.forEach((range):void => {
                selection?.addRange(range);
            });
        }

        active?.focus();
    };
}

tm.copyPasteManger = function(text: string, options: { format?: string }):boolean {
    let reselectPrevious: () => void;
    let range;
    let selection;
    let tempElement;
    let success = false;

    try {
        reselectPrevious = deselectCurrent();

        range = document.createRange();
        selection = document.getSelection();

        tempElement = document.createElement(`span`);
        tempElement.textContent = text;
        // reset user styles for span element
        tempElement.style.all = `unset`;
        // prevents scrolling to the end of the page
        tempElement.style.position = `fixed`;
        tempElement.style.top = `0`;
        tempElement.style.clip = `rect(0, 0, 0, 0)`;
        // used to preserve spaces and line breaks
        tempElement.style.whiteSpace = `pre`;
        // do not inherit user-select (it may be `none`)
        tempElement.style.webkitUserSelect = `text`;
        tempElement.style.MozUserSelect = `text`;
        tempElement.style.msUserSelect = `text`;
        tempElement.style.userSelect = `text`;
        tempElement.addEventListener(`copy`, (e: ClipboardEvent) => {
            e.stopPropagation();

            if (options.format) {
                e.preventDefault();

                if (typeof e.clipboardData === "undefined") { // IE 11
                    console.warn(`unable to use e.clipboardData`);
                    console.warn(`trying IE specific stuff`);

                    window.clipboardData.clearData();

                    let format: string = clipboardToIE11Formatting[options.format] || clipboardToIE11Formatting[`default`];

                    window.clipboardData.setData(format, text);
                } else { // all other browsers
                    e.clipboardData?.clearData();
                    e.clipboardData?.setData(options.format, text);
                }
            }
        });

        document.body.appendChild(tempElement);

        range.selectNodeContents(tempElement);
        selection?.addRange(range);

        window.__TESTMAKER__CLIPBOARD__ = text;

        let successful = document.execCommand(`copy`);

        if (!successful) throw new Error(`copy command was unsuccessful`);

        success = true;
    } catch (err) {
        console.warn(`unable to copy using execCommand: `, err);
        console.warn(`trying IE Method`);
        try {
            window.clipboardData.setData(options.format || `text`, text);
            success = true;
        } catch (err) {
            console.warn(`unable to copy using clipboardData: `, err);
            console.warn(`falling back to Custom Method`);
            window.__TESTMAKER__CLIPBOARD__ = text;
        }
    } finally {
        if (selection) {
            typeof selection.removeRange === `function`
                ? selection.removeRange(range)
                : selection.removeAllRanges();
        }

        if (tempElement) document.body.removeChild(tempElement);

        reselectPrevious();
    }

    return success;
}
