export function waitUntil(
    {
        predicate,
        timeout,
        interval,
        message,
        overrideMessage
    }: { predicate: boolean | (() => boolean) | (() => Promise<boolean>), timeout?: number, interval?: number, message?: string, overrideMessage?: string }
) {
    let timerInterval = interval || 50;
    let timerTimeout = timeout || 3000;

    return new Promise((resolve, reject: (reason?: any) => void): void => {
        let timer: NodeJS.Timeout;
        let timeoutTimer: NodeJS.Timeout;
        let clearTimers: () => void = (): void => {
            clearTimeout(timeoutTimer);
            clearInterval(timer);
        };

        let doStep = async (): void => {
            let result;

            try {
                result = typeof predicate === `function` ? await predicate() : predicate;
                if (result) {
                    clearTimers();
                    resolve(result);
                } else {
                    timer = setTimeout(doStep, timerInterval);
                }
            } catch (e) {
                clearTimers();
                reject(e);
            }
        };

        timer = setTimeout(doStep, timerInterval);

        timeoutTimer = setTimeout((): void => {
                clearTimers();

                reject(new Error(overrideMessage ? overrideMessage : `Timed out after waiting for ${timerTimeout}ms${message ? ` ${message}` : ``} `));
            }
            , timerTimeout);
    });
}
