import { io } from 'socket.io-client';
import { serializeError } from '../../../helpers/error-serializer';
import { waitUntil } from '../../../helpers/wait-until';
import { ClientTestMaker } from '../../../types/client-test-maker';

const globalTestMakerName =`__TEST__MAKER__`
// @ts-ignore
const tm: ClientTestMaker = window[`${globalTestMakerName}`] as ClientTestMaker;

tm.eventsManager = {
    events: {}
};

tm.eventsManager.on = async (method: string, data: { [key: string]: any }): Promise<any> => {
    return tm.eventsManager.events[`${method}`](data);
};

tm.eventsManager.emit = async (method: string, data: { [key: string]: any }): Promise<any> => {
    let result: any;

    tm.socketClient.emit(`execute`, {
        sessionId: tm.session.uid,
        method: method,
        data: data
    }, (response: any) => {
        result = response;
    });

    return new Promise(resolve => {
        waitUntil({
            predicate: () => {
                return !!result;
            },
            timeout: 3000
        }).then(() => {
            resolve(result);
        }).catch(_ => resolve(result));
    });
};

tm.socketClient = io(`http://localhost:${tm.configuration.socketPort}`, {
    query: `sessionId=${tm.session.uid}`,
    transports: [
        `websocket`,
        `polling`,
        `flashsocket`
    ],
    extraHeaders: {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Private-Network': `true`,
    }
});

tm.socketClient.on(`connect`, (): void => {
    console.log(`Test Maker Client Socket is connected`);
});

tm.socketClient.on(`disconnect`, (): void => {
    console.log(`Test Maker Client Socket is disconnected`);
});

tm.socketClient.on(`${tm.session.uid}:execute`, async (args: { method: string, data: { [key: string]: any } }, callback: (result: any) => void): Promise<any> => {
    let result;

    try {
        result = { data: await tm.eventsManager.on(`${args.method}`, args.data) };
    } catch (e) {
        result = {
            error: serializeError(e),
            data: {}
        };
        console.log(e);
    }

    if (typeof callback === `function`) callback(result);
});
