import Swal from 'sweetalert2';
import { ClientTestMaker } from '../../../types/client-test-maker';

const globalTestMakerName =`__TEST__MAKER__`
// @ts-ignore
const tm: ClientTestMaker = window[`${globalTestMakerName}`] as ClientTestMaker;

let notification;

function showNativeDialogHint(dialogType: string) {
    notification = Swal.fire({
        title: 'Test Maker Native Dialog Hint',
        html: `<span style="font-weight: bold; color: #af9068">${dialogType}</span> Native Dialog is about to be active, Make sure there is a Native Dialog Handler added to handle it to avoid breaking the Test.`,
        icon: `warning`,
        toast: true,
        width: `70%`,
        timerProgressBar: true,
        showCancelButton: false,
        showConfirmButton: false,
        position: tm?.configuration?.nativeDialogsHint?.position || `top`,
        timer: tm?.configuration?.nativeDialogsHint?.closeTimeOut || 4000,
        showClass: {
            backdrop: 'swal2-noanimation', // disable backdrop animation
            popup: '',                     // disable popup animation
            icon: ''                       // disable icon animation
        },
        hideClass: {
            popup: '',                     // disable popup fade-out animation
        }
    });
}

if (tm?.configuration.nativeDialogsHint.alert) {
    (function (proxied: any): void {
        window.alert = function () {
            showNativeDialogHint(`Alert`);

            return proxied.apply(this, arguments);
        };
    })(window.alert);
}

if (tm?.configuration.nativeDialogsHint.prompt) {
    (function (proxied: any) {
        window.prompt = function (text: string, defaultText: string): string {
            showNativeDialogHint(`Prompt`);


            return proxied.apply(this, arguments);
        };
    })(window.prompt);
}

if (tm?.configuration.nativeDialogsHint.confirm) {
    (function (proxied: any): void {
        window.confirm = function () {
            showNativeDialogHint(`Confirm`);

            return proxied.apply(this, arguments);
        };
    })(window.confirm);
}

if (tm?.configuration.nativeDialogsHint.onbeforeunload) {
    (function (proxied: any): void {

        window.onbeforeunload = function () {
            showNativeDialogHint(`Onbeforeunload`);

            return proxied.apply(this, arguments);
        };
    })(window.onbeforeunload);
}
