import { ClientTestMaker } from "../../../types/client-test-maker";

const globalTestMakerName =`__TEST__MAKER__`
const tm: ClientTestMaker = window[`${globalTestMakerName}`] as ClientTestMaker;

tm.clientLogMessages = {
    log: [],
info: [],
warn: [],
error: []
};

const oldConsoleLog: { (message?: any, ...optionalParams: any[]): void; (...data: any[]): void } = console.log;
const oldConsoleInfo: { (message?: any, ...optionalParams: any[]): void; (...data: any[]): void } = console.info;
const oldConsoleWarn: { (message?: any, ...optionalParams: any[]): void; (...data: any[]): void } = console.warn;
const oldConsoleError: { (message?: any, ...optionalParams: any[]): void; (...data: any[]): void } = console.error;

console.log = function(...args: any[]){
    oldConsoleLog.apply(console, args);
    storeLog(args, `log`);
}

console.info = function(...args: any[]){
    oldConsoleInfo.apply(console, args);
    storeLog(args, `info`);
}

console.warn = function(...args: any[]){
    oldConsoleWarn.apply(console, args);
    storeLog(args, `warn`);
}

console.error = function(...args: any[]){
    oldConsoleError.apply(console, args);
    storeLog(args, `error`);
}

function storeLog(args: any[], type: string) {
    const message= args[0];
    switch (type) {
        case `info`:
            tm.clientLogMessages.info.push(message);
            break;
        case `warn`:
            tm.clientLogMessages.warn.push(message);
            break;
        case `error`:
            tm.clientLogMessages.error.push(message);
            break;
        default:
            tm.clientLogMessages.log.push(message);
    }
}
